import React, { Component } from 'react';
import { Redirect }         from "react-router-dom";
import auth0                from 'auth0-js';
import API                  from './server_api'

import './Login.css'

let accessToken = null;
let idToken     = null;
let me          = null;
let expiresAt   = 0;

const LOGIN_URL   = window.location.origin +  window.location.pathname;
const CLIENT_ID   = '7b4QizTZk0ptdfV5X3mcseQZJ0vLzJbB'; // API APP
const AUTH_DOMAIN = 'nodelfilms.eu.auth0.com';

const auth = new auth0.WebAuth({
    domain:         AUTH_DOMAIN,
    clientID:       CLIENT_ID,
    redirectUri:    LOGIN_URL,
    responseType:   'token id_token',
    scope:          'openid profile email'
});

function loggedIn (authResult) {
    // сохраняем данные из токена
    expiresAt   = (authResult.expiresIn * 1000) + new Date().getTime();
    accessToken = authResult.accessToken;
    idToken     = authResult.idToken;
    // Set isLoggedIn flag in localStorage
    localStorage.setItem('isLoggedIn', 'true');
}

export function getToken(){
    return idToken;
}

export function getMe(){
    return me;
}

export function updateMe(json){
    me=json;
}

export function isAuthenticated () {
    if ( accessToken === null || me === null ) {
        return false;
    }
    else {
        const now = new Date().getTime();
        // через 10 минут устареет? надо бы обновить. Обычно они на два часа выдаются
        if ( now + 10*60*1000 >= expiresAt ) {
            auth.checkSession({}, (err, authResult) => {
                if (authResult && authResult.accessToken && authResult.idToken)
                    loggedIn(authResult);
            });
        } 
        // пока считаем, что все норм, токен есть и он не очень старый, либо вот-вот обновится
        return true;
    }
}

export class Login extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            logged_in:   isAuthenticated(), // есть ли уже токен доступа к серверу и  информация о пользователе ?

            restoring_session: false,       // на время восстановления старой сессии не будем ничего рисовать на экране, чтобы не мелькало
            return_to:   { pathname: '/' }, // куда вернемся после восстановления сессии - если не было сюда явного редиректа - вернемся в корень

            inputEmail:  '',                // поля ввода в форме
            inputCode:   '',

            wrong_email: false,             // состояния UI
            code_sent:   false,
        };

        // запоминаем, откуда нас сюда заредиректили, чтобы потом вернуться
        if ( this.props.location.state !== undefined )
            this.state.return_to =  this.props.location.state.from;

        // может быть не аутентифицированы сейчас, но были при прошлом посещении страницы?
        let was_logged_before = localStorage.getItem('isLoggedIn');
        if ( accessToken === null &&  was_logged_before ) {
            // флаг, предотвращающий мелькание экрана
            this.state.restoring_session = true;
            // на случай ошибки сбрасываем флаг наличия сессии, чтобы не было бесконечного цикла
            localStorage.removeItem('isLoggedIn');
            // пытаемся восстановить токен с сервера 
            auth.checkSession({}, (err, authResult) => {
                if (authResult && authResult.accessToken && authResult.idToken) {
                    loggedIn(authResult);
                    API.getMe((json) => {
                        me = json;
                        this.setState({ logged_in: true })
                    });
                }
                else
                    // попытка залогиниться не прошла, рисуем форму логина
                    this.setState({ restoring_session: false })
            });
        }

        // странная ситуация - есть токен, но не загружена информация о себе - загружаем
        if ( accessToken !== null && me === null ) {
            API.getMe((json) => {
                me = json;
                this.setState({ logged_in: true })
            });
        }

        // Вариант для HashRouter - обрабатываем callback от Auth0 с токеном в URL
        if ( props.match.params.hash !== undefined ){
            // флаг, предотвращающий мелькание экрана
            this.state.restoring_session = true;
            auth.parseHash( 
                { hash: '#access_token=' + props.match.params.hash },
                (err, authResult) => {
                    if ( authResult && authResult.accessToken && authResult.idToken ) {
                        loggedIn(authResult);
                        API.getMe((json) => {
                            me = json;
                            this.setState({ logged_in: true })
                        });
                    }
                    else 
                        // попытка залогиниться не прошла, рисуем форму логина
                        this.setState({ restoring_session: false })
                }
            );
        }

        this.handleInputChange = this.handleInputChange.bind(this);
        this.performLogIn      = this.performLogIn.bind(this);
    }

    handleInputChange(event) {
        const target = event.target;
        this.setState({ [target.id]: target.value, wrong_email: false });
    }

    performLogIn(event){
        if ( this.state.code_sent === false ){ 
            auth.passwordlessStart({ 
                connection: 'email', 
                send: 'code', 
                email: this.state.inputEmail,
            }, (err,res) => {
                if ( err !== null )
                    this.setState ({ code_sent: false, wrong_email: true });
                else 
                    this.setState ({ code_sent: true, wrong_email: false });
            });
        }
        else {
            auth.passwordlessLogin({ 
                connection: 'email', 
                email: this.state.inputEmail,
                verificationCode: this.state.inputCode,
            }, (err,res) => {
                console.log('err=', err);
                console.log('res=', res);
            });
        }
        event.preventDefault();
    }
    
    render () {
        const no_user = this.state.wrong_email;

        if ( this.state.logged_in )
            return <Redirect to={this.state.return_to} />;
        else if ( this.state.restoring_session )
            return <center><h4 className='p-4 text-muted'>Logging in, please wait...</h4></center> ; // не мелькаем экраном на вермя восстановления сессии
        else
            return (
            <main>
                <form className="form-signin text-center my-5" onSubmit = { this.performLogIn }>
                    <img className="mb-4" src="https://www.nodelfilms.com/assets/icons/nodelfilms-icon.svg" alt="" width="72" height="72" />
                    <h1 className="h3 mb-3 font-weight-normal">
                        Вход только для зарегистрированны пользователей
                    </h1>
                    {
                        this.state.code_sent && 
                        <p className='text-secondary'>
                            Код подтверждения отправлен на указанную почту. Для входа, введите код в поле ниже:
                        </p>
                    }
                    <div className="input-group">
                        { this.state.code_sent || 
                            <div className="input-group-prepend">
                                <span className="input-group-text">@</span>
                            </div>
                        }
                    <input type= { this.state.code_sent ? 'hidden' : 'email' }
                           id="inputEmail" 
                           className={no_user ? "form-control is-invalid" : "form-control"}
                           placeholder="Email" 
                           value={this.state.inputEmail} 
                           onChange={this.handleInputChange}
                           autoFocus={true}/>
                    { no_user &&
                        <div className="invalid-feedback">
                            Пользователь не зарегистрирован
                        </div>
                    }
                    </div>
                    <input type={ this.state.code_sent ? 'number' : 'hidden' }
                           id="inputCode" 
                           className="form-control" 
                           placeholder="Код из письма" 
                           value={this.state.inputCode} 
                           onChange={this.handleInputChange}
                           autoFocus={true}/>
                    <button className="btn btn-primary btn-block my-2" type="submit">
                        { this.state.code_sent ? ' Проверить код' : ' Получить код для входа' }
                    </button>
                    <small>
                        <a href="/" onClick={ (event)=>{ auth.authorize(); event.preventDefault(); } }>
                            Вход по паролю
                        </a>
                    </small>
                    <p className="mt-5 mb-3 text-muted">
                        © 2019, nodelfilms.com
                    </p>
                </form>
            </main> 
        );
    }    
}

export function Logout (props) {
    if ( accessToken !== null ){
        // Remove isLoggedIn flag from localStorage
        me = null;
        localStorage.removeItem('isLoggedIn');
        auth.logout( { returnTo: LOGIN_URL, clientID: CLIENT_ID });
    }
    // это будет отображено на экране на короткое время, 
    // пока идет запрос к серверу авторизации
    // потом будет редирект страницу логина
    return (<center><h4 className='p-4 text-muted'>Logging out, please wait...</h4></center>);
}